:root {
  --nav-bg: #6b705c;
  --bg: #edefde;
  --card-bg: #8288719e;
  --heading: #3f4238;
  /* box-shadow: 2px 2px 3px #6b705c; */
}

/* Style for the playlist section */
/* .playlist-section {
    padding: 20px;
    border-radius: 8px;
    margin-bottom: 20px;
} */

/* Style for the playlist header */
.playlist-header {
  display: flex;
  align-items: center;
  margin: 15px;
  /* border-bottom: 1px solid #ccc; */
  /* border-bottom: 1px solid var(--heading); */
}

.playlist-header img {
  /* max-width: 100px; */
  border-radius: 4px;
  margin: 15px;
}

.playlist-details h2 {
  margin: 10px;
  /* color: whitesmoke; */
  /* color: var(--heading); */
  font-size: 24px;
  font-family: Heebo;
}

.playlist-details p {
  margin: 5px 10px;
  font-size: 16px;
  /* color: #888; */
  /* color: var(--heading); */
  font-family: Abel;
}

.total-songs {
  font-weight: bold;
  color: #ff6600;
}

.playlist-details .arrow-button {
  width: 40px;
  height: 40px;
  padding: 8px 9px;
  color: #555;
  transition: background-color 0.3s, color 0.3s;
  margin-right: 15px;
  position: relative;
  top: 15px;
  left: 10px;
}

.playlist-songs {
  display: flex;
  flex-direction: column;
}

.recent-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  border-radius: 10px;
  width: 94%;
  margin: 10px auto;
  /* background-color: #333; */
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); */
  /* background-color: var(--card-bg); */
  /* box-shadow: 2px 2px 3px #6b705c; */
  font-family: Jost;
  transition: transform 0.3s ease-in-out;
}

.recent-item:hover {
  transform: scale(1.01);
}

.song-info {
  display: flex;
  align-items: center;
}

.song-info img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 15px;
}

.details {
  color: #fff;
  font-family: Jost;
  color: var(--heading);
}

.details h3 {
  margin: 5px 0;
  font-size: 18px;
}

.details p {
  margin: 3px 0;
  font-size: 14px;
}

.duration {
  color: #aaa;
  color: var(--heading);
  margin-bottom: 0;
}

/* Style for the arrow button */
.arrow-button {
  text-decoration: none;
  border: 1px solid #ccc;
  border-radius: 50%;
  font-size: 18px;
  color: #555;
  transition: background-color 0.3s, color 0.3s;
  display: flex;
  justify-content: center;
  align-content: center;
  font-weight: bold;
  font-size: 20px;
}

.arrow-button:hover {
  background-color: #f0f0f0;
  color: #333;
}

/* Style for the song actions container */
.song-actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 10%;
}

@media screen and (max-width: 1200px) {
  .song-actions {
    width: 15%;
  }
}

@media screen and (max-width: 700px) {
  .song-actions {
    width: 18%;
  }
}

@media screen and (max-width: 560px) {
  .playlist-header img {
    width: 160px;
  }

  .song-actions {
    width: 24%;
  }

  .playlist-details .arrow-button {
    top: 7px;
  }

  .playlist-details h2 {
    font-size: 20px;
  }

  .recent-item {
    padding: 8px;
  }

  .playlist-details p {
    font-size: 14px;
  }
}

@media screen and (max-width: 400px) {
  .playlist-header img {
    width: 120px;
  }

  .playlist-details .arrow-button {
    top: 4px;
    width: 30px;
    height: 31px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .song-actions {
    width: 30%;
  }

  .arrow-button {
    font-size: 16px;
  }

  .details h3 {
    font-size: 16px;
  }

  .details p {
    font-size: 12px;
  }

  .recent-item {
    padding: 6px;
  }

  .song-info img {
    width: 45px;
    height: 45px;
  }
}

@media screen and (max-width: 340px) {
  .playlist-header {
    margin: 10px;
  }

  .playlist-header img {
    width: 110px;
  }

  .playlist-details h2 {
    font-size: 16px;
  }

  .playlist-details p {
    font-size: 12px;
  }

  .arrow-button {
    font-size: 15px;
  }
}
