:root {
  --nav-bg: #6b705c;
  --bg: #edefde;
  --card-bg: #8288719e;
  --heading: #3f4238;
  /* --box-shadow: 2px 2px 3px #6b705c; */
}

/* Style for the playlist header */
.playlist-header-1 {
  display: flex;
  align-items: center;
  margin: 15px;
  /* border-bottom: 1px solid #ccc; */
}

.playlist-header-1 .icon img {
  /* max-width: 100px; */
  border-radius: 4px;
  margin: 15px;
}

.playlist-details-1 h2 {
  margin: 10px;
  /* color: whitesmoke; */
  /* color: var(--heading); */
  font-size: 24px;
  margin-bottom: 25px;
  font-family: Abel;
}

.playlist-details-1 #openModalBtn {
  margin: 5px 10px;
  font-size: 16px;
  color: #888;
  background: black;
  padding: 10px 14px;
  border-radius: 34px;
}

.playlist-section .heading-1 {
  margin: 10px;
  /* color: whitesmoke; */
  font-size: 24px;
  margin-bottom: 25px;
  margin-left: 25px;
  margin-left: 38px;
  /* border-bottom: 1px solid #ccc; */
  /* border-bottom: 1px solid var(--heading); */
  /* color: var(--heading); */
  padding-bottom: 14px;
  font-family: Arimo;
}

.playlist-songs {
  display: flex;
  flex-direction: column;
}

/* Style for the container */
.recent-item-1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  border-radius: 10px;
  width: 94%;
  margin: 10px auto;
  /* background-color: #333; */
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); */
  /* background-color: var(--card-bg); */
  /* box-shadow: 2px 2px 3px #6b705c; */
  font-family: Jost;
  transition: transform 0.3s ease-in-out;
}

.recent-item-1:hover {
  transform: scale(1.015);
}
.song-info-1 {
  display: flex;
  align-items: center;
}

.song-info-1 img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 15px;
}

/* .details-1 {
  color: #fff;
  color: var(--heading);
} */

.details-1 h3 {
  margin: 5px 0;
  font-size: 18px;
}

.details-1 p {
  margin: 3px 0;
  font-size: 14px;
}

.duration {
  /* color: #aaa;
  color: var(--heading); */
  margin-bottom: 0;
}

.arrow-button {
  text-decoration: none;
  border: 1px solid #ccc;
  border-radius: 50%;
  font-size: 18px;
  color: #555;
  transition: background-color 0.3s, color 0.3s;
  display: flex;
  justify-content: center;
  align-content: center;
  font-weight: bold;
  font-size: 20px;
}

.arrow-button:hover {
  background-color: #f0f0f0;
  color: #333;
}

/* Style for the song actions container */
.song-actions-1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 10%;
}

/* Styles for the modal */
.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

/* Styles for modal content */
.modal-content {
  /* background-color: #202124;
  background-color: var(--heading); */
  margin: 10% auto;
  padding: 20px;
  border-radius: 10px;
  max-width: 400px;
  position: relative;
  text-align: center;
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  box-shadow: 2px 2px 10px #6b705c; */
}

.modal-content img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 0 auto;
  margin-bottom: 20px;
}

/* Styles for the close button */
.close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  font-size: 24px;
  color: #888;
}

/* Styles for form inputs and buttons */
.input-field {
  position: relative;
  margin-bottom: 20px;
}

#profileForm input {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  width: 80%;
  box-sizing: border-box;
  background-color: var(--input-bg);
}
#profileForm input::placeholder {
  color: white;
}

.edit-icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  color: #888;
}

#saveUsernameBtn {
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  /* background-color: black; */
  color: white;
  font-family: Heebo;
  /* background-color: var(--nav-bg); */
  transition: transform 0.3s ease-in-out;
}

#saveUsernameBtn:hover {
  transform: scale(1.1);
}

/* Style the trigger button */
#openModalBtn {
  padding: 10px 20px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  /* background: black; */
  /* background-color: var(--nav-bg); */
  color: white;
  font-family: Heebo;
  border-radius: 34px;
  transition: transform 0.3s ease-in-out;
}

#openModalBtn:hover {
  transform: scale(1.1);
  background: #333;
}

@media screen and (max-width: 1200px) {
  .song-actions-1 {
    width: 15%;
  }
}

@media screen and (max-width: 700px) {
  .song-actions-1 {
    width: 18%;
  }

  .playlist-section .heading-1 {
    margin: 20px;
    width: 93%;
  }

  .modal-content {
    margin: 24% auto;
  }
}

@media screen and (max-width: 490px) {
  .modal-content {
    margin: 34% auto;
    padding: 25px 5px;
    max-width: 328px;
  }
}

@media screen and (max-width: 560px) {
  .playlist-header-1 .icon img {
    max-width: 100px;
  }

  .playlist-header-1 img {
    width: 160px;
  }

  .song-actions-1 {
    width: 24%;
  }

  .playlist-details-1 .arrow-button {
    top: 7px;
  }

  .playlist-details-1 h2 {
    font-size: 19px;
  }

  .recent-item-1 {
    padding: 8px;
  }

  .playlist-details-1 p {
    font-size: 14px;
  }

  .playlist-section .heading-1 {
    width: 91%;
  }
}

@media screen and (max-width: 400px) {
  .modal-content {
    margin: 49% auto;
    padding: 24px 0px;
    max-width: 290px;
  }

  .playlist-header-1 {
    display: flex;
    align-items: center;
    margin: 5px;
  }

  #openModalBtn {
    padding: 6px 15px;
  }

  .playlist-details-1 h2 {
    margin-top: 5px;
    margin-bottom: 17px;
  }

  .playlist-details-1 .arrow-button {
    top: 4px;
    width: 30px;
    height: 31px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .song-actions-1 {
    width: 30%;
  }

  .arrow-button {
    font-size: 16px;
  }

  .details-1 h3 {
    font-size: 16px;
  }

  .details-1 p {
    font-size: 12px;
  }

  .recent-item-1 {
    padding: 6px;
  }

  .song-info-1 img {
    width: 45px;
    height: 45px;
  }

  .playlist-section .heading-1 {
    width: 89%;
    margin: 17px;
    font-size: 19px;
  }
}

@media screen and (max-width: 340px) {
  .playlist-header-1 .icon img {
    max-width: 88px;
  }

  .playlist-header-1 {
    margin: 10px;
  }

  .playlist-header-1 img {
    width: 110px;
  }

  .playlist-details-1 h2 {
    font-size: 15px;
  }

  .playlist-details-1 p {
    font-size: 12px;
  }

  .arrow-button {
    font-size: 15px;
  }

  .playlist-section .heading-1 {
    width: 86%;
  }

  .modal-content img {
    width: 80px;
    height: 80px;
  }

  .modal-content {
    margin: 58% auto;
    padding: 24px 0px;
    max-width: 257px;
  }
}
