:root {
  --nav-bg: #6b705c;
  --bg: #edefde;
  --card-bg: #8288719e;
  --heading: #3f4238;
  /* --box-shadow: 2px 2px 3px #6b705c; */
}

.search {
  margin-top: 35px;
  margin-left: 20px;
}

.search input {
  width: 99%;
  height: 38px;
  border-radius: 20px;
  border: none;
  padding-left: 40px;
  font-size: 16px;
  /* background-color: rgb(40, 40, 40); */
  /* background-color: var(--card-bg); */
  /* color: white; */
  /* color: var(--heading); */
}

.search i {
  position: relative;
  left: 10px;
  top: 31px;
  color: white;
}

.type {
  margin-top: 20px;
  margin-left: 20px;
  padding: 10px;
  /* font-family: Arimo; */
}

.item-type {
  padding: 10px;
  border-radius: 20px;
  /* background-color: rgb(40, 40, 40); */
  /* background-color: var(--nav-bg); */
  color: white;
  margin-right: 10px;
  cursor: pointer;
  border: none;
  transition: tranform 0.3s ease-in-out;
}

.item-type:hover {
  transform: scale(1.1);
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 93%;
  margin: 25px auto;
  margin-bottom: 25px;
  font-family: Arimo;
}

.title h1 {
  font-size: 20px;
  font-weight: 500;
  /* color: white; */
  /* color: var(--heading); */
  margin: 0;
}

.title #clear {
  /* font-size: 14px; */
  font-weight: 500;
  color: white;
  cursor: pointer;
  padding: 10px 18px;
  background-color: rgb(40, 40, 40);
  border-radius: 20px;
}

/* Style for the container */
.recent-item-2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  /* border: 1px solid #ccc; */
  border-radius: 20px;
  width: 95%;
  margin: 10px auto;
  /* background-color: #282828; */
  /* background-color: var(--card-bg); */
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.recent-item-2:hover {
  transform: scale(1.015);
}

/* Style for the song image */
.recent-item-2 img {
  width: 50px;
  height: 50px;
  /* Adjust image size as needed */
  border-radius: 40px;
  margin-right: 10px;
}

/* Style for the song details */
.song-details {
  flex-grow: 1;
}

.song-details h3 {
  margin: 0;
  font-size: 18px;
  /* color: white;
  color: var(--heading); */
  font-family: Jost;
}

.song-details p {
  margin: 5px 0;
  font-size: 14px;
  color: #888;
  color: var(--heading);
  font-family: Jost;
}

/* Style for the arrow button */
.arrow-button-1 {
  text-decoration: none;
  padding: 8px 12px;
  border: 1px solid #ccc;
  border-radius: 50%;
  font-size: 18px;
  color: #555;
  transition: background-color 0.3s, color 0.3s;
}

.arrow-button-1:hover {
  background-color: #f0f0f0;
  color: #333;
}

@media screen and (max-width: 960px) {
  .type {
    margin-top: 12px;
  }

  .search {
    margin-left: 0;
  }

  .search i {
    left: 38px;
    top: 2px;
  }

  .search input {
    width: 90%;
  }

  .container-1 #basedlike {
    color: white;
    color: var(--heading);
    font-size: 29px;
    margin-top: 25px;
  }

  .song-inner-container {
    margin: 0;
    padding: 18px 0;
  }
}

@media screen and (max-width: 400px) {
  .search input {
    width: 86%;
  }

  .search i {
    left: 33px;
  }

  .title {
    width: 91%;
    margin-left: 20px;
    margin-bottom: 15px;
  }

  .container-1 #basedlike {
    font-size: 23px;
    margin-top: 25px;
    margin-left: 22px;
  }

  .inner-container {
    padding: 5px;
    min-height: 230px;
  }
}
